  z-index: 1;*/
  -webkit-transform: translateX(-30%);
  transform: translateX(-30%);
  -webkit-transition: all 0.3s ease;
  transition: all 0.3s ease;
}
.slide-right-leave-active {
  position: absolute;
  z-index: 10001;
  -webkit-transform: translateX(0);
  transform: translateX(0);
  -webkit-transition: all 0.3s ease;
  transition: all 0.3s ease;
}
